.landing-page {
  display: inline-block;
  justify-content: center;
  align-items: center;

  .slide-container {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 0;
  }

  .img{
    width:1280px;
    height:960px;
    @media all and (width: 414px) and (height: 736px) {
      display: flex;
      width: 400px;
      height: 480px;
    }

    @media all and (max-width: 360px) and (height: 640px)  {
      display: flex;
      height: 640px;
      width: 360px;
    }

    @media all and (width: 411px) and (height: 731px)  {
      display: flex;
      height: 731px;
      width: 411px;
    }

    @media all and (width: 411px)  and (height: 823px) {
      display: flex;
      height: 823px;
      width: 411px;
    }

    @media all and (width: 320px)  and (height: 568px) {
      display: flex;
      height: 568px;
      width: 320px;
    }

    @media all and (width: 375px) and (height: 812px){
      display: flex;
        width: 375px;
        height: 812px;
      }
      
    @media all and (width: 375px) and (height: 667px) {
      display: flex;
      width: 375px;
      height: 667px;
    }
      

    @media all and (width: 414px) and (height: 736px) {
      display: flex;
      height: 736px;
      width: 414px;
    }
   
    @media all and (width: 375px) and (height: 667px) {
      display: flex;
      height: 667px;
      width: 375px;
    }
  }

  h3 {
    text-align: center;
  }

  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 300px;
  }

  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }

  .each-fade {
    display: flex;
  }

  .each-fade .image-container {
    width: 75%;
    overflow: hidden;
  }

  .each-fade .image-container img {
    width: 100%;
  }

  .each-fade h2 {
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #adceed;
  }
}
