.video{
    display: flex;
    align-items: center;
    justify-content: center;
    
    width:1280px;
    height:720px;
    border-top-width: 3px;
    border-right-width: 3px;
    border-bottom-width: 3px;
    border-left-width: 3px;

    @media all and (width: 414px) and (height: 736px) {
      display: block;
      width: 400px;
      height: 480px;
    }

    @media all and (max-width: 360px) and (height: 640px)  {
      display: block;
      height: 640px;
      width: 360px;
    }

    @media all and (width: 411px) and (height: 731px)  {
      display: block;
      height: 731px;
      width: 411px;
    }

    @media all and (width: 411px)  and (height: 823px) {
      display: block;
      height: 823px;
      width: 411px;
    }

    @media all and (width: 320px)  and (height: 568px) {
      display: block;
      height: 568px;
      width: 320px;
    }

    @media all and (width: 375px) and (height: 812px){
      display: block;
        width: 375px;
        height: 812px;
      }
      
    @media all and (width: 375px) and (height: 667px) {
      display: block;
      width: 375px;
      height: 667px;
    }
      

    @media all and (width: 414px) and (height: 736px) {
      display: block;
      height: 736px;
      width: 414px;
    }
   
    @media all and (width: 375px) and (height: 667px) {
      display: block;
      height: 667px;
      width: 375px;
    }

}