
  body {
    margin: 0;
    // background: url("./svg/bg_img.png") no-repeat center center fixed;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color:slategrey
  }
  
  #root{
    background-image: linear-gradient(to top right, transparent 15%, white 300%);
  }
  
  html, body, #root, .main-container{
    display: inline-grid;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
    margin: 0 0 0 0;
    justify-content: stretch;
    align-items: center;
    margin: auto;
   
    @media all and (max-width: 360px) and (height: 640px)  {
      display: inline-grid;
      height: 640px;
      width: 360px;
    }

    @media all and (width: 411px) and (height: 731px)  {
      display: inline-grid;
      height: 731px;
      width: 411px;
    }

    @media all and (width: 411px)  and (height: 823px) {
      display: inline-grid;
      height: 823px;
      width: 411px;
    }

    @media all and (width: 320px)  and (height: 568px) {
      display: inline-grid;
      height: 568px;
      width: 320px;
    }

    @media all and (width: 375px) and (height: 812px){
      display: inline-grid;
        width: 375px;
        height: 812px;
      }
      
    @media all and (width: 375px) and (height: 667px) {
      width: 375px;
      height: 667px;
    }
      

    @media all and (width: 414px) and (height: 736px) {
      display: inline-grid;
      height: 736px;
      width: 414px;
    }
   
    @media all and (width: 375px) and (height: 667px) {
      display: inline-grid;
      height: 667px;
      width: 375px;
    }
}
