.audio{
    display: flex;
    align-items: center;
    justify-content: center;
    

    @media all and (width: 414px) and (height: 736px) {
        display: contents;
        width: 400px;
        height: 480px;
      }
  
      @media all and (max-width: 360px) and (height: 640px)  {
        display: contents;
        height: 640px;
        width: 360px;
      }
  
      @media all and (width: 411px) and (height: 731px)  {
        display: contents;
        height: 731px;
        width: 411px;
      }
  
      @media all and (width: 411px)  and (height: 823px) {
        display: contents;
        height: 823px;
        width: 411px;
      }
  
      @media all and (width: 320px)  and (height: 568px) {
        display: contents;
        height: 568px;
        width: 320px;
      }
  
      @media all and (width: 375px) and (height: 812px){
        display: contents;
          width: 375px;
          height: 812px;
        }
        
      @media all and (width: 375px) and (height: 667px) {
        display: contents;
        width: 375px;
        height: 667px;
      }
        
  
      @media all and (width: 414px) and (height: 736px) {
        display: contents;
        height: 736px;
        width: 414px;
      }
     
      @media all and (width: 375px) and (height: 667px) {
        display: contents;
        height: 667px;
        width: 375px;
      }

}